import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
import uniPopup from "@/components/uni-popup/uni-popup.vue"

export default {
	components: {tabControl,uniPopup},
	data() {
		return {
			current: 0,
			m:false,
			loans_list:'',
			condition:''
		}
	},
	methods: {
		toDetail(id,type) {
			uni.navigateTo({
				url:"../loansdetail/loansdetail?id="+id+"&type="+type
			})
		},
		open(index){
			this.$refs.popup.open()
			this.condition = this.loans_list[index].rule_tips
		},
		close(){
			this.$refs.popup.close()
		}
	},
	onLoad() {
		this.request.httpRequest('api/loan6/index','GET').then(res => {
			this.loans_list = res.data.retval.list_data
		})
	}
}
